import { Link } from 'react-router-dom';

import logo from '../../images/Pizzarela-Artesanal.png';
import './header.css';

const Header = () => {

  return (
    <header>
      <div><Link to={`/`} ><img src={logo} alt="Pizzarela-Artesanal" /></Link></div>
    </header>
    
  );
};

export default Header;