import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'

import { fetchPaidOrders } from '../../../app/store/paidOrdersSlice'
import UseDisplayOrders from '../../../app/hooks/useDisplayOrders'

import './onlineOrders.css'

function OnlineOrders() {
  
  const [newPizza, setNewPizza] = useState([]);
  const paidOrdersStatus = useSelector(state => state.paidOrders.status);
  const paidOrdersValues = useSelector(state => state.paidOrders.value);
  const paidOrdersError = useSelector(state => state.paidOrders.error);
  
  const dispatch = useDispatch();

  setInterval(() => {
    window.location.reload()
    // location.reload();
  }, 60000); 

  useEffect(() => {
    if (paidOrdersStatus === 'idle') {
      dispatch(fetchPaidOrders());
    }
  }, [dispatch, paidOrdersStatus]);

  useEffect(() => {
    if (paidOrdersStatus === 'succeeded') {
      setNewPizza(paidOrdersValues)
    }
  }, [paidOrdersStatus, paidOrdersValues])

  let orders;
  if ( paidOrdersStatus === 'loading') {
    orders = <h2>Cargando Órdenes...</h2>
  } else if ( paidOrdersStatus === 'succeeded') {
    orders = (
      <React.Fragment>
        {/* <h1>Órdenes Online</h1> */}
        {
          newPizza.map((e, i) => {
              if (e === 'No hay órdenes nuevas.') {
                return <h2 key={i}>{e}</h2>
              } else {
                return <UseDisplayOrders e={e} key={`${e.id}`} />
              }
          })
        }
      </React.Fragment>
    );
  } else if ( paidOrdersStatus === 'failed') {
    orders = <section>{paidOrdersError}</section>
  }

  return (
    <section className='onlineOrders'>
      {orders}
    </section>
  );

}

export default OnlineOrders