import { 
  BrowserRouter as Router,
  Switch,
  Route
} from 'react-router-dom';

import Header from './components/header/Header';
import Orders from './components/orders/Orders'

function App() {

  return (
    <Router basename={'/vistaCajero'}>
      <Header />
      <div className="App">
        <Switch>
          <Route path={`/`} component={Orders} />
        </Switch>
      </div>
    </Router>
  );
}

export default App;