import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';

import orders from '../../apis/orders';

const initialState = {
  value: [],
  status: 'idle',
  error: null,
}

export const fetchPaidOrders = createAsyncThunk('paidOrders/fetchPaidOrders', async () => {
  const response = await orders();
  console.log('Not sorted: ', response)
  // You are making pizzas[Array].id order by ascendant in order to dislay promos in groups of three
  if (response[0] !== "No hay órdenes nuevas.") response.forEach(element => element.pizzas.sort((a, b) => a.id - b.id));
  // console.log('Sorted: ', response)
  return response;
});

const paidOrders = createSlice({
  name: 'paidOrders',
  initialState,
  reducers: {},
  extraReducers: {
    [fetchPaidOrders.pending]: (state, action) => {
      state.status = 'loading';
    },
    [fetchPaidOrders.fulfilled]: (state, action) => {
      state.status = 'succeeded';
      state.value = state.value.concat(action.payload);
    },
    [fetchPaidOrders.rejected]: (state, action) => {
      state.status = 'failed';
      state.error = action.error.message;
    },
  }
});

export default paidOrders.reducer;