import React from 'react'
import updateProcess from '../../apis/updateProcess'

function useDisplayOrders({ e }) {

  const update = ({ target }) => {
    updateProcess({id: e.id, proceso: target.value}).catch(err => alert(`${err} Proceso no actualizado. Orden ${e.id}`));
  }

  // For rendering
  let renderTipo;
  let renderIndividuales = [];
  let renderPromociones = [];

  if (e.tipo === 'A') {
    renderTipo = 'Individual y Promoción'
  } else if (e.tipo === 'P') {
    renderTipo = 'Promoción'
  } else if (e.tipo === 'I') {
    renderTipo = 'Individual'
  }

  // Orders of promos and individuals
  let promociones = [];
  let individuales = [];

  // Group by 3 (+1) or 2 (+1) elements in arr, because of promos
  let j = 1;
  let promo = [];
  for (let i = 0; i < e.pizzas.length; i++) {
    if (e.pizzas[i].grupo === 'Pack Familiar 1' || e.pizzas[i].grupo === 'Pack Familiar 2' || 
    e.pizzas[i].grupo === 'Promo para Compartir' || e.pizzas[i].grupo === 'Promo Entre Patas') {
      if (j <= 4) {
        promo.push(e.pizzas[i])
        j++;
      } 
      if (j === 4) {
        promociones.push(promo);
        promo = []
        j = 1;
      }
    } else if (e.pizzas[i].grupo === 'Dúos Grandes') {
      if (j <= 3) {
        promo.push(e.pizzas[i])
        j++;
      } 
      if (j === 3) {
        promociones.push(promo);
        promo = []
        j = 1;
      }
    } else {
      individuales.push(e.pizzas[i])
    }
  }

  renderIndividuales = (individuales.length > 0) && (
    <section className='pizzasIndividuales'>
      <section><span style={{fontWeight: 700}}>Pizzas Individuales:</span></section>
      {
        individuales.map(element => {
          return (
            <section className='pizzasInfoIndividuales' key={`${element.id}`} >
              <span style={{fontWeight: 700, color: '#DB9423'}}>Nombre: </span>{element.pizza} {element.medida}&nbsp;
              <span style={{fontWeight: 700, color: '#DB9423'}}>Cantidad: </span>{element.cantidad}
            </section>
          )
        })
      }
    </section>
  );
  renderPromociones = (promociones.length > 0) && (
    <section className='pizzasPromociones'>
      <section><span style={{fontWeight: 700}}>Pizzas Promociones:</span></section>
      {
        promociones.map(element => {
          return (
            <section className='pizzasPromocionesDetails' key={`${element[0].id}-${element[1].id}`}>
              <section className='promoName' style={{fontWeight: 600, color: '#db5d23'}}>{element[0].grupo}:</section>
              {element.map(element1 => {
                return (
                  <section className='pizzasInfoPromociones' key={`${element1.id}`} >
                    <span style={{fontWeight: 700, color: '#DB9423'}}>Nombre: </span>{element1.pizza} {element1.medida}&nbsp;
                    <span style={{fontWeight: 700, color: '#DB9423'}}>Cantidad: </span>{element1.cantidad}
                  </section>
                )
              })}
            </section>
          )
        })
      }
    </section>
  );
  
  return (
    <section className='details' >
      <section className='InfoProcess'>
        <section className='info'>
          <section><span style={{fontWeight: 700}}>Id de orden:</span> <span style={{fontWeight: 700, color: '#db5d23'}}>{e.id}</span></section>
          <section><span style={{fontWeight: 700}}>Hora: </span>{e.hora}</section>
          <section><span style={{fontWeight: 700}}>Id de transacción: </span>{e.transaccion_id}</section>
          <section><span style={{fontWeight: 700}}>Tipo: </span>{renderTipo}</section>
          <section><span style={{fontWeight: 700}}>Cliente: </span>{e.nombres} {e.apellidos}</section>
        </section>
        <section className='process'>
          <section><span style={{fontWeight: 700}}>Proceso:</span></section>
          <input type="radio" name={`${e.id}`} value="En espera" defaultChecked={e.proceso === 'En espera'} onChange={update}/> 1.- En espera <br />
          <input type="radio" name={`${e.id}`} value="Preparando" defaultChecked={e.proceso === 'Preparando'} onChange={update}/> 2.- Preparando
          <br />
          <input type="radio" name={`${e.id}`} value="Listo" defaultChecked={e.proceso === 'Listo'} onChange={update}/> 3.- Listo<br />
          <input type="radio" name={`${e.id}`} value="Entregado" defaultChecked={e.proceso === 'Entregado'} onChange={update}/> 4.- Entregado<br />
        </section>
      </section>
      {
        e.entrega === 'local' ?
        <section><span style={{fontWeight: 700}}>Entrega: </span>Local</section>
        :
        (
          <React.Fragment>
            <section ><span style={{fontWeight: 700}}>Teléfono: </span>{e.telefono}</section>
            <section><span style={{fontWeight: 700}}>Entrega: </span>{`${e.distrito}, ${e.urbanizacion}, ${e.calle} ${e.numero}, apartamento ${e.apartamento}, bloque ${e.bloque}.`}</section>
            <section ><span style={{fontWeight: 700}}>Detalles: </span>{e.detalles}</section>
          </React.Fragment>
        )
      }
      {renderIndividuales}
      {renderPromociones}
    </section>  
  )
}

export default useDisplayOrders





